<template>
  <div class="address">
    <van-nav-bar
      left-text="返回"
      title="地址列表"
      left-arrow
      fixed
      @click-left="goback"
    >
    </van-nav-bar>

    <div class="box">
      <BgBox>
         <div class="list" v-if="list.length > 0">
            <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="add"
            @edit="edit"
          />
         </div>
         <div v-else>
           <van-empty description="没有地址数据" />
         </div>
      </BgBox>
    </div>
  </div>
</template>

<script>
import BgBox from "../components/BgBox.vue";
export default {
  components: { BgBox },
  name: "Address",
 data(){
     return{
         chosenAddressId:'',
         list:[],
         
     }
 },

 created() {
     this.findAddress()
 },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    // 添加地址
    add(){
     this.$router.push({name:'NewAddress'})
    },
    // 编辑地址
    edit(item){
     this.$router.push({name:'NewAddress',query:{aid:item.aid}})
    },

    // 查询地址
    findAddress(){

        let tokenString = localStorage.getItem('tokenString');

        if(!tokenString){
            this.$toast("请先登录账户");
            return this.$toast.push({name:'Login'});

        }

       this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0
        });

        this.axios({
            method:'GET',
            url:'/findAddress',
            params:{
                appkey:this.appkey,
                tokenString
            }
        }).then(result =>{
            this.$toast.clear()
          if(result.data.code == 700){
              this.$toast(result.data.msg)
          }else if(result.data.code == 20000){
             
             result.data.result.map(v =>{
                 v.isDefault = Number(v.isDefault);
                 v.id = v.aid;
                 v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`;
             })

              this.list = result.data.result;
          }

        }).catch(err =>{
            this.$toast.clear()
            console.log(err)
        })
    }
  },
};
</script>

<style lang="less" scoped>

.address{

  
    padding-top: 46px;
 /deep/ .van-address-list {
  padding: 0;

}
 /deep/.van-address-item {
  border-bottom: 1px solid #e8e8e8;
    // padding: 0;
}

  /deep/.van-radio__icon .van-icon{
  display:none;
}
 /deep/.van-address-item {
  border-radius: 0px;
}

 /deep/.van-address-item:not(:last-child) {
  margin-bottom: 0;
}

   /deep/.van-nav-bar__text{
        color:  #994a1b;
    }

    /deep/ .van-nav-bar .van-icon {
        color:  #994a1b;
    }

      /deep/ .van-button--danger{
        background-color:#780d00;
        border-color: #780d00;
    }
        
      /deep/.van-tag--danger{
      background-color: #780d00;
    }
  

}
</style>